scrollTop = _this.$el.scrollTop > 20 ? _this.$el.scrollTop - 20 : 0\r\n                }\r\n            })\r\n        }\r\n    }\r\n}\r\n\r\n</script>\r\n<style type=\"text/css\">\r\n    .edit {\r\n        overflow: hidden;\r\n        background: white;\r\n    }\r\n\r\n    .scroll-box {\r\n        overflow: auto;\r\n    }\r\n</style>"],"sourceRoot":""}]);

